@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
:root {
  --textColor: #f43b47;
  --textDefaultColor: rgba(0, 0, 0, 0.8);
  --listDivider: rgba(0, 0, 0, 0.3);
  --headerTextColor: #9D2053;
  --headerBackground: rgba(52, 25, 49, 0.7);
  --backgroundGradient: linear-gradient(to top, #f43b47 0%, #453a94 100%);
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  /*
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

label {
  color: #f43b47;
  color: var(--textColor);
  margin-right: 12px;
  font-size: 25px;
}

html {
  color: rgba(0, 0, 0, 0.8);
  color: var(--textDefaultColor);
  background-image: -webkit-gradient(linear, left bottom, left top, from(#f43b47), to(#453a94));
  background-image: -webkit-linear-gradient(bottom, #f43b47 0%, #453a94 100%);
  background-image: linear-gradient(to top, #f43b47 0%, #453a94 100%);
  background-image: var(--backgroundGradient);
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: #f43b47;
  color: var(--textColor);
  opacity: 0.4;
}

::-moz-placeholder {
  color: #f43b47;
  color: var(--textColor);
  opacity: 0.4;
}

:-ms-input-placeholder {
  color: #f43b47;
  color: var(--textColor);
  opacity: 0.4;
}

::-ms-input-placeholder {
  color: #f43b47;
  color: var(--textColor);
  opacity: 0.4;
}

::placeholder {
  color: #f43b47;
  color: var(--textColor);
  opacity: 0.4;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  color: var(--headerTextColor);
  top: 0;
  font-size: 30px;
  font-weight: bold;
}

footer {
  color: var(--textColor);
  bottom: 0;
  font-size: 20px;
  font-weight: bold;
}

header .fa-bars,
footer i {
  margin-right: 10px;
}

header, footer {
  position: fixed;
  left: 0;
  right: 0;
  overflow: hidden;
}

footer {
  padding: 16px;
}

header .container {
  display: flex;
  justify-content: space-between;
  padding: 16px 16px 16px 66px;
}

.header-background {
  margin: -40px;
  -webkit-filter: blur(8px);
          filter: blur(8px);
  background: var(--headerBackground);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;

  margin-top: 66px;
  margin-bottom: 54px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
}

li {
  padding: 12px;
  border-bottom: 1px solid var(--listDivider);
  margin: 8px;
  position: relative;
}

.bm-item {
  display: inline-block;
  text-decoration: none;
  margin-bottom: 10px;
  color: var(--textColor);
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}

.bm-item:hover {
  color: white;
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 20px;
}

.bm-burger-bars {
  background: var(--headerTextColor);
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  background: var(--headerBackground);
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

.bm-morph-shape {
  fill: #373a47;
}

.bm-item-list {
  color: #b8b7ad;
  display: flex;
  flex-direction: column;
}

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.bm-overlay,
.bm-menu-wrap {
  top: 0;
}

.bm-item:last-child {
  margin-top: auto;
}

.fa-spinner {
  -webkit-animation: App-logo-spin infinite 2s linear;
          animation: App-logo-spin infinite 2s linear;
}


.search-hidden input {
  display: none;
}

.search-hidden span {
  display: inline-block;
}


.search-visible input {
  display: inline-block;
}

.search-visible span {
  display: none;
}

input {
  border: 1px solid #9d2053;
  background: rgba(0,0,0,0.4);
  padding: 4px;
  font-size: 25px;
  margin: -4px 0;
  color: var(--textColor);
}

#items-loading {
  width: 100px;
  height: 100px;
  margin-left: -50px;
  left: 50%;
  top: 50%;
  position: absolute;
  font-size: 50px;
}

.hidden {
  opacity: 0;
}

#bars {
  display: none;
  height: 26px;
  margin: -22px 0 0 -8px;
  position: absolute;
  width: 40px;
}

#bars.selected {
  display: block;
}

.bar {
  background: #f43b47;
  bottom: 1px;
  height: 3px;
  position: absolute;
  width: 3px;      
  -webkit-animation: sound 0ms -800ms linear infinite alternate;      
          animation: sound 0ms -800ms linear infinite alternate;
}

@-webkit-keyframes sound {
  0% {
     opacity: .35;
      height: 3px; 
  }
  100% {
      opacity: 1;       
      height: 28px;        
  }
}

@keyframes sound {
  0% {
     opacity: .35;
      height: 3px; 
  }
  100% {
      opacity: 1;       
      height: 28px;        
  }
}

.bar:nth-child(1)  { left: 1px; -webkit-animation-duration: 474ms; animation-duration: 474ms; }
.bar:nth-child(2)  { left: 5px; -webkit-animation-duration: 433ms; animation-duration: 433ms; }
.bar:nth-child(3)  { left: 9px; -webkit-animation-duration: 407ms; animation-duration: 407ms; }
.bar:nth-child(4)  { left: 13px; -webkit-animation-duration: 458ms; animation-duration: 458ms; }
.bar:nth-child(5)  { left: 17px; -webkit-animation-duration: 400ms; animation-duration: 400ms; }
.bar:nth-child(6)  { left: 21px; -webkit-animation-duration: 427ms; animation-duration: 427ms; }
.bar:nth-child(7)  { left: 25px; -webkit-animation-duration: 441ms; animation-duration: 441ms; }
.bar:nth-child(8)  { left: 29px; -webkit-animation-duration: 419ms; animation-duration: 419ms; }
.bar:nth-child(9)  { left: 33px; -webkit-animation-duration: 487ms; animation-duration: 487ms; }
.bar:nth-child(10) { left: 37px; -webkit-animation-duration: 442ms; animation-duration: 442ms; }

#settings {
  position: absolute;
  top: 0;
  padding: 20px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 11111;
  background: rgba(0,0,0,0.7);
  color: var(--textColor);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

#close {
  position: absolute;
  right: 20px;
  color: var(--textColor);
}

.right {
  float: right;
}


.menu {
  position: fixed;
  bottom: 50px;
  right: 10px;
  background: rgba(0,0,0,0.7);
  padding: 20px;
}

.menu a {
  text-decoration: none;
  display: block;
  color: var(--textColor);
}

.menu a + a {
  margin-top: 12px;
}

button {
  padding: 8px 30px;
  font-size: 18px;
  border: 1px solid #f43a47;
  color: #f43a47;
  background: #9d2053;
}

.full-width {
  width: 100%;
}
